.header-color{
  background: #141E30;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #141E30, #141E30);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #141E30, #141E30); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.mdl-layout__drawer-button{
  background: #141E30;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #141E30, #141E30);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #141E30, #141E30); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid{
  background: #141E30;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #3E8989 , #141E30);  /* Chrome 10-25, Safari 5.1-6*/
  background: linear-gradient(to top, #3E8989 , #141E30); /*W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+*/
  height: 100%;
  background-position: center;
  background-size: initial; 
  text-align: center;
  /**243B55*/
  position: fixed;
  top: 60px;
  left: 0;
  width: 100%;
  height: 100%;
}

.avatar-img{
  height: 200px;
  padding-top: 1em;
  margin: 20px;

}

.banner-text{
  background-color: transparent;
  /* background-color: black   ***version 1  */
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  /*color: white;    version 1 */
  background: -webkit-linear-gradient(white, #d1e3f5);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}
.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p{
  color: white;
  font-size: 20px;
  padding: 10px;
}

