   .social-links-contact{
    display: flex;
    justify-content: space-between;
    margin-left: auto;
  margin-right: auto;
  width: 50%;
    
  }
  
  .social-links-contact i{
    color: white;
    font-size: 5em;
    padding: 8px;
    cursor: pointer;
  } 

  .contact-banner-text{
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
  }

  .contact-banner-text h2{
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    color: white;
    text-align: center;
}

 .contact-banner-text img{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
} 

.image-effect-container {
  position: relative;
  display: block;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background: linear-gradient(to top, #41295a , #2f0743); 
}

.contact-banner-text:hover .overlay{
  opacity: 1;
}

.contact-image-text {
  color: white;
  font-size: 20px;
  font-weight: bold;
    font-family: 'Montserrat', sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}


  /* About me Page */

.about-wrapper{
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 600px){
  .projects-grid-react{
    display: flex;
    width: 100%;
    transform: scale(0.5);
  }
  .card-title1 .card-title2 .card-title3 .card-title4{
    display: flex;
    transform: scale(0.5);
  }
  .category-tabs{
    margin-top: 5px;
    transform: scale(0.5);
    width: 100%;
  }
}


