/*/ SMALL DEVICES /*/
@media only screen and (max-width: 426px) {
  .mdl-layout__header {
    min-height: 56px;
    height: 8%;
}

  .banner-text h1{
    font-size: 40px;
  }
  .category-tabs{
    top: 5px;
    height: 100%;
    width: 100%;
    
  }
  .projects-grid-react{
    width: fit-content;
  }
  .new-contact-grid h1{
    font-size: 40px;
  }
  .new-contact-grid h2{
    font-size: 25px;
  }
  .contact-banner-text{
    text-align: center;
  }
  .social-links-contact{
    display: grid;
    grid-template-columns: auto auto;
    justify-items: start;
  }
  .image-effect-container{
    width: 100%;
  max-width: 400px;
  height: auto;
  }

}

/*/ MEDIUM DEVICES /*/
@media only screen and (max-width: 768px) {
  .mdl-layout__header {
    min-height: 56px;
    height: 8%;
}
  .category-tabs{
    display: flex;
    flex-direction: column;
  }
  .projects-grid-react{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
  .image-effect-container{
    width: 100%;
  max-width: 600px;
  height: auto;
  }
  }

/*/ LARGE DEVICES /*/
@media only screen and (min-width: 920px) and (max-width: 1199px) {
  body {
    background-color: black;
  }
  .mdl-layout__header {
    min-height: 56px;
    height: 8%;
}
  .category-tabs{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .projects-grid-react{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .image-effect-container{
    width: 100%;
    max-width: 600px;
    height: auto;
  }

  }

